/* ============= MAIN ============= */
main {
  padding: 0;
  display: flex;
}

/* ============= SIDEBAR ============= */
.sidebar {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100;
  width: 230px;
  height: 100vh;
  background: #1C1C1D;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.sidebar_brand {
  width: 100%;
  padding: 20px;
}
.sidebar_brand .img_container {
  width: 30px;
  height: 30px;
  background: #46464C;
  border-radius: 50%;
}
.sidebar_brand p {
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-left: 20px;
}
.sidebar_list {
  width: 100%;
}
.sidebar_list .sidebar_item .sidebar_link {
  width: 100%;
  padding: 20px;
  color: rgba(255, 255, 255, 0.6);
}
.sidebar_list .sidebar_item .sidebar_link .bx {
  font-size: 30px;
}
.sidebar_list .sidebar_item .sidebar_link span {
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-left: 20px;
}
.sidebar_list .sidebar_item .sidebar_link.active_link {
  background: #46464C;
  color: rgba(255, 255, 255, 0.8);
}
.sidebar_list .sidebar_item .sidebar_link:not(.active_link):hover {
  background: rgba(70, 70, 76, 0.7);
}
.sidebar .btn_deconnexion {
  width: 100%;
  padding: 20px;
}
.sidebar .btn_deconnexion a {
  width: 100%;
  color: rgba(255, 255, 255, 0.6);
}
.sidebar .btn_deconnexion a .bx {
  font-size: 30px;
}
.sidebar .btn_deconnexion a span {
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-left: 20px;
}
.sidebar .btn_deconnexion:hover {
  background: #46464C;
}

/* ============= ADMIN CONTAINER ============= */
#admin-container .title_container {
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.6);
}
#admin-container .container_half_block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#admin-container .container_half_block .half_block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(50% - 10px);
  padding: 20px;
  font-size: 16px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.8);
}
#admin-container .container_half_block .half_block .bx {
  margin-right: 10px;
}
#admin-container .container_block {
  margin-bottom: 25px;
  background: #1C1C1D;
  box-shadow: 0px 5px 20px rgba(28, 28, 29, 0.5);
}
#admin-container .container_block table {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
}
#admin-container .container_block table thead {
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
#admin-container .container_block table thead th {
  font-weight: normal;
}
#admin-container .container_block table th,
#admin-container .container_block table td {
  overflow: hidden;
  padding: 15px;
}
#admin-container .container_block table .signal_user {
  background: #46464C;
}
#admin-container .container_block table .tab_tools,
#admin-container .container_block table .action_tab {
  width: 120px;
}
#admin-container .container_block table .tab_tools .bx,
#admin-container .container_block table .action_tab .bx {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #46464C;
  border-radius: 5px;
  margin: 0 auto;
}
#admin-container .container_block table .tab_tools .bx:nth-of-type(2),
#admin-container .container_block table .action_tab .bx:nth-of-type(2) {
  margin-left: 10px;
}
#admin-container .container_block_title {
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
#admin-container .container_block_content {
  padding: 20px;
}
#admin-container .container_block_content .slide_list .slide_item {
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #313138;
}
#admin-container .container_block_content .slide_list .slide_item:not(:last-child) {
  border-bottom: 1px solid #1C1C1D;
}
#admin-container .container_block_content .slide_list .slide_item:last-child {
  margin-bottom: 20px;
}
#admin-container .container_block_content .slide_list .slide_item .img_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 75px;
  background: #46464C;
}
#admin-container .container_block_content .slide_list .slide_item .slide_tools .bx {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  background: #46464C;
  border-radius: 5px;
}
#admin-container .container_block_content .create_new_slide_container,
#admin-container .container_block_content .create_new_actus_container,
#admin-container .container_block_content .create_new_team_container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  margin-bottom: 20px;
  background: #313138;
}
#admin-container .container_block_content .create_new_slide_container .bx,
#admin-container .container_block_content .create_new_actus_container .bx,
#admin-container .container_block_content .create_new_team_container .bx {
  margin-right: 10px;
}
#admin-container .container_block_content .footer_btn .add_new_slide {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 20px;
  background: #46464C;
  border-radius: 30px;
}
#admin-container .container_block_content .footer_btn .add_new_slide .bx {
  font-size: 16px;
  margin-right: 10px;
}
#admin-container .container_block_content .footer_btn button[type=submit] {
  padding: 15px 20px;
  border-radius: 30px;
  margin-right: 10px;
  font-size: 16px;
}
#admin-container .container_block form .checkbox {
  margin-bottom: 20px;
}
#admin-container .container_block form .checkbox input {
  margin-right: 5px;
}
#admin-container .container_block form .checkbox input:nth-of-type(2) {
  margin-left: 20px;
}
#admin-container .container_block form h3 {
  margin-bottom: 10px;
}
#admin-container .container_block form .social_fields {
  margin-bottom: 20px;
}
#admin-container .container_block form .social_fields .bx {
  width: 59px;
  height: 59px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #46464C;
}
#admin-container .container_block form .social_fields input {
  width: calc(100% - 59px) !important;
  margin-bottom: 0 !important;
}
#admin-container .container_block form input[type=text],
#admin-container .container_block form textarea {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px;
  background: #313138;
}
#admin-container .container_block form textarea {
  resize: none;
}
#admin-container .container_block form button[type=submit] {
  min-width: 100px;
  padding: 15px 20px;
  border-radius: 30px;
  margin-right: 10px;
  font-size: 16px;
}
#admin-container .container_block input[type=file] {
  display: none;
}